.loading .ant-spin{
    position: absolute;
    left: calc(50% - 30px);
    top: calc(50% - 30px);
}
.wrapper{
    .header{
        h1{
            margin: 0;
            padding: 24px;
            padding-bottom: 0;
            img{
                display: block;
                height: 32px;
            }
        }
        h2{
            margin: 0;
            padding: 0 24px;
            line-height: 32px;
            font-size: 18px;
            font-weight: 400;
            sup{
                top: 6px;
                right: -30px;
            }
        }
        .btnList{
            padding: 24px;
            float: right;
            button{
                margin-left: 10px;
            }
        }
        .swList{
            padding-top: 10px;
            text-align: right;
        }
    }
    .footer{
        h2{
            margin: 0;
            padding: 24px;
            padding-top: 0;
            font-size: 14px;
            font-weight: 400;
        }
    }
    .navRight{
        a{
            font-size: 12px !important;
        }
        .ant-anchor-ink::before{
            width: 1px;
        }
    }
    .main{
        padding: 0 24px;
        .ant-about,
        .ant-card,
        .ant-table{
            margin-bottom: 24px;
        }
        .ant-html{
            margin-bottom: 24px !important;
            background: #fcfcfc !important;
            border: 1px solid #e8e8e8 !important;
        }
        .ant-btn-lg{
            display: block;
            margin: 0 auto;
            margin-bottom: 24px;
            width: 50%;
        }
    }
}

.wrapper.dark_lock{
    background: #131418;
    .ant-card{
        background: #2d303a;
        color: #fff;
        .ant-card-head{
            color: #fff;
            background: #2d303a;
        }
    }
    .ant-table{
        background: #2d303a;
        color: #fff;
        .ant-table-thead th{
            color: #fff;
            background: #2d303a;
        }
    }
    .ant-anchor-wrapper{
        background: #2d303a;
        color: #fff;
        a{
            color: #fff;
        }
    }
}